import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

export type AvatarSize = 'xxs' | 'xs' | 'sm' | 'md' | 'lg' | 'xl' | '2xl';
export type AvatarType = 'image' | 'placeholder' | 'text';
export type AvatarStatus = 'online' | 'offline' | 'none';

@Component({
  tag: 'bh-avatar',
  styleUrl: 'bh-avatar.css',
  shadow: true,
})
export class BhAvatar {
  /**
   * The size of the avatar
   */
  @Prop() size: AvatarSize = 'md';

  /**
   * The type of avatar content
   */
  @Prop() type: AvatarType = 'image';

  /**
   * The status indicator (online, offline, or none)
   */
  @Prop() status: AvatarStatus = 'none';

  /**
   * Text to display for text-type avatars (initials)
   */
  @Prop() text: string = 'RD';

  /**
   * Image source URL for image-type avatars
   */
  @Prop() imageSrc?: string;

  /**
   * Alt text for the avatar image
   */
  @Prop() alt: string = 'Avatar';

  /**
   * Emitted when the avatar is hovered
   */
  @Event() bhMouseEnter!: EventEmitter<void>;

  /**
   * Emitted when the mouse leaves the avatar
   */
  @Event() bhMouseLeave!: EventEmitter<void>;

  private handleMouseEnter = (): void => {
    this.bhMouseEnter.emit();
  };

  private handleMouseLeave = (): void => {
    this.bhMouseLeave.emit();
  };

  private getTextSizeClass(): string {
    const sizeMap: Record<AvatarSize, string> = {
      'xxs': 'text-xxs',
      'xs': 'text-xs',
      'sm': 'text-sm',
      'md': 'text-md',
      'lg': 'text-lg',
      'xl': 'text-xl',
      '2xl': 'text-2xl',
    };
    return sizeMap[this.size] || 'text-md';
  }

  private getIconSizeClass(): string {
    const sizeMap: Record<AvatarSize, string> = {
      'xxs': 'icon-sm',
      'xs': 'icon-md',
      'sm': 'icon-lg',
      'md': 'icon-xl',
      'lg': 'icon-2xl',
      'xl': 'icon-3xl',
      '2xl': 'icon-4xl',
    };
    return sizeMap[this.size] || 'icon-xl';
  }

  render() {
    const avatarClasses = {
      'avatar': true,
      [`avatar-${this.size}`]: true,
      'avatar-with-status': this.status !== 'none',
    };

    const statusClasses = {
      'avatar-status': true,
      [`avatar-status-${this.size}`]: true,
      'avatar-status-online': this.status === 'online',
      'avatar-status-offline': this.status === 'offline',
    };

    return (
      <div
        class={avatarClasses}
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
        part="avatar"
      >
        <div class="avatar-border"></div>

        <div class="avatar-content">
          {this.type === 'image' && (
            <img
              src={this.imageSrc}
              alt={this.alt}
              class="avatar-image"
            />
          )}

          {this.type === 'placeholder' && (
            <span class={`material-symbols-outlined avatar-placeholder-icon ${this.getIconSizeClass()}`}>
              person
            </span>
          )}

          {this.type === 'text' && (
            <span class={`avatar-text ${this.getTextSizeClass()}`}>
              {this.text}
            </span>
          )}
        </div>

        {this.status !== 'none' && (
          <div class={statusClasses}></div>
        )}
      </div>
    );
  }
}
